import { _decorator, Component, Node, Sprite } from 'cc';
import { AudioMgr } from '../utils/AudioMgr';
const { ccclass, property } = _decorator;
if (globalThis.eventTargets == null) {
    globalThis.eventTargets = new EventTarget();
}
@ccclass('sgj_run')
export class sgj_run extends Component {

    startIndex = 0;
    endIndex = 0;
    runCount = 72;

    start() {
        globalThis.sgj_run = this;
        this.play(7);
    }

    update(deltaTime: number) {

    }

    play(endIndex) {
        this.startIndex = 0;
        this.endIndex = endIndex + this.runCount; //中奖格子加上圈数
        this.resetMask();
        this.schedule(this.updateMask, 0.6);
        AudioMgr.inst.playOneShot('sound/sgj/turnstep1');
    }

    stop() {
        this.unschedule(this.updateMask);
    }

    resetMask() {
        for (let i = 0; i < 24; i++) {
            globalThis.sgj_view.mask_node[i].active = false;
            globalThis.sgj_view.light_node[i].active = false;
        }
    }

    updateMask() {
        //中奖结束回调
        if (this.startIndex == this.endIndex) {
            this.unschedule(this.updateMask);
            globalThis.eventTargets.emit('sgj_callback', '中奖结束!');
            return;
        }

        for (let i = 0; i < 24; i++) {
            //当前高亮的格子
            if (this.startIndex == i) {
                globalThis.sgj_view.mask_node[i].active = true;
                globalThis.sgj_view.light_node[i].active = true;
            }
            else {
                globalThis.sgj_view.mask_node[i].active = false;
                globalThis.sgj_view.light_node[i].active = false;
            }
        }



    }
}



